import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ModalController } from 'ionic-angular';
import { AngularFirestore } from '@angular/fire/firestore';
import { Observable } from 'rxjs';
import { PayTemplate, Collections } from '../../app/app.models';
import { take } from 'rxjs/operators';

@IonicPage()
@Component({
  selector: 'page-pay-templates',
  templateUrl: 'pay-templates.html',
})
export class PayTemplatesPage {

  public templates$: Observable<PayTemplate[]> = this.afs
    .collection<PayTemplate>(Collections.PayTemplate)
    .valueChanges();

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private afs: AngularFirestore,
    private modal: ModalController,
  ) {
  }

  async openAdd() {
    const m = await this.modal.create('PayTemplateAddPage');
    await m.present();
  }

  async removeTemplate(id: string) {
    await this.afs.collection(Collections.PayTemplate).doc(id).delete();
  }

  async editTemplate(id: string) {
    const template = await this.afs.collection(Collections.PayTemplate)
      .doc(id)
      .valueChanges()
      .pipe(take(1))
      .toPromise();
    const m = await this.modal.create('PayTemplateEditPage', { template });
    await m.present();
  }

}



